@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.4.1/font/bootstrap-icons.css);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
:root {
    --primary: #009688;
    --offset: #f1f1f1;

    --header-font: 'OpenSans', sans-serif;
    --text-font: 'Roboto', sans-serif;
}

html, body {
    margin: 0;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'OpenSans', sans-serif;
    font-family: var(--header-font);
    font-weight: 600;
}

ul {
    list-style: none;
    padding: 0;
}

ul > li {
    padding: 0.2rem 0;
}

.App {
    background-color: #f1f1f1;
    background-color: var(--offset);
    font-family: 'Roboto', sans-serif;
    font-family: var(--text-font);

    min-height: 100vh;
}

.App > .AppGrid {
    padding: 1rem 0;

    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 1rem;
    gap: 1rem;
}

@media (min-width: 768px) { 
    .App > .AppGrid {
        padding: 1rem 0;
    
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
}

@media (min-width: 992px) { 
    .App > .AppGrid {
        margin: 0 15%;
        padding: 1rem 0;
    
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-gap: 1rem;
        gap: 1rem;
    }
}

.AppGrid > .AppPanel {
    background-color: #FFF;
    box-shadow: 0 2px 5px 0 rgb(0 0 0 / 16%), 0 2px 10px 0 rgb(0 0 0 / 12%);
    padding: 1rem;
}

.bi {
    color: #009688;
    color: var(--primary);
    font-size: 1.2rem;
}
/*
    Define the Size of the MainPanel on the grid view
*/
/* Desktop */
@media (min-width: 768px) { 
    .MainPanel {
        grid-row: 1 / 3;
    }
}

/*
    Escape the parent's padding by reversing it.
    Additionally at bottom some space for the name is created.
*/
.MainPanel > .ProfileImage {
    margin: -1rem -1rem -2.5rem -1rem;
}

.MainPanel > .ProfileName {
    font-family: var(--header-font);
    font-size: 2rem;

    position: relative;
    top: -0.5rem;
}
@media (min-width: 768px) { 
    .EducationPanel {
        grid-column: 2 / 4;
    }
}
@media (min-width: 768px) { 
    .WorkPanel {
        grid-column: 2 / 4;
    }
}

